import React, { Component, useState } from "react";
import "../styles/App.css";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Massage: null
    };
  }
  handleclick = (e) => {
    e.preventDefault();
    //console.log("hello");
    this.setState({
      Massage: ` Hello, I've learnt to use the full-stack evaluation tool. This makes me so happy`
    });
  };

  render() {
    return (
      <div id="main">
        <button id="click" onClick={this.handleclick}>
          click
        </button>
        <p id="para">{this.state.Massage}</p>
      </div>
    );
  }
}

export default App;
